.App {
  text-align: center;
}

.Mrgn-banner {
  color: #004043;
  font-size: 45px;
  margin: 40px;
}

.Mrgn-screen {
  width : 1000px; 
  height : 350px;
  margin: 5px;
  
}

.Mrgn-btn{
  margin: 0px;
  padding: 25px;
  background-color:#FF8C00;
}

.Mrgn-gears {
  margin: 10px;
}

.Common {
  display: flex;
  justify-content: center;
  user-select: none;
}

.btn{
  margin:40px 30px;
}

.Arrows{
  margin:20px 30px;
}

@media only screen and (max-width: 700px) {
  .Mrgn-screen {
    width : 300px; 
    height : 300px;
    margin: 15px;
  }

  .Arrows{
    margin:20px 1px;
    flex-grow: 3;
  }
  
  .Mrgn-btn{
    margin: 1px;
    padding: 20px;
    background-color:#B1ABC4;
  }
  .Mrgn-banner {
    color: #FFD018;
    font-size: 35px;
    margin: 40px 20px 15px 20px;
  }

  .PNP{
    padding: 0px;

  }

  .btn{
    flex-grow: 1;
  }
  
  

}
